.header {
    width: 70%;
    height: 70px;
    border-bottom: black solid 1px;
    border-left: black solid 1px;
    border-bottom-left-radius: 20px;
    position: fixed;
    top: 0;
    right: -100%;
    animation: slideIn 0.5s forwards;
    font-size: 21px;
    margin-bottom: 10px;
}

header {
    margin-bottom: 10px;
}

nav {
    display: flex;
    align-items: center;
}

a {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.2em;
    padding: 15px 0;
    text-decoration: none;
    color: #000;
    font-weight: 500;
}

.visible {
    right: 0;
}

.hidden {
    right: -100%;
}

@keyframes slideIn {
    from {
        transform: translateX(100%);
    }

    to {
        transform: translateX(0);
    }
}

div {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 15px;
}

main {
    margin-top: 100px;
}